@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/all.min.css);
body{margin:0;font-family:'Raleway', sans-serif;background:ghostwhite}#root{width:100vw;height:80vh;display:flex;align-items:center;justify-content:center}

.app{text-align:center}.app h1{letter-spacing:0.2em;font-size:40px;color:#989898}.app h2{letter-spacing:0.1em;font-size:30px;color:#989898}.app a,.app a:visited,.app a:active{color:#989898;font-size:32px}.app a:hover,.app a:visited:hover,.app a:active:hover{color:#bbbbbb;-webkit-transition:color 0.4s;transition:color 0.4s}.app p{font-size:42px;margin-top:10px}

nav{display:flex;justify-content:space-around;align-items:center;min-height:10vh;background-color:#282c34;color:#fff}nav a,nav a:visited,nav a:active{color:#fff;text-decoration:none}nav .nav-links{width:40%;display:flex;justify-content:space-around;align-items:center;list-style:none;padding:0}



