.app{
  text-align: center;

  h1{
    letter-spacing: 0.2em;
    font-size: 40px;
    color: #989898;
  }

  h2{
    letter-spacing: 0.1em;
    font-size: 30px;
    color: #989898;
  }

  a, a:visited, a:active{
    color: #989898;
    font-size: 32px;

    &:hover{
      color: #bbbbbb;;
      transition: color 0.4s;
    }
  }

  p{
    font-size: 42px;
    margin-top: 10px;
  }
}
