nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background-color: #282c34;
  color: #fff;

  a, a:visited, a:active{
    color: #fff;
    text-decoration: none;
  }

  .nav-links{
    width: 40%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
    padding: 0;
  }
}
